.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 0s linear;
          animation: App-logo-spin infinite 0s linear;
  height: 100px;
  width: 100px;
}

.App-header {
  background-color: black;
  padding: 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  height: 10px;
}

.App-title {
  font-size: 1.5em;
}

.React {
  color: blue;
}

.Character {
  margin-top: 2%;
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5%;
}

.Character img {
  height: 100px;
  border-radius: 50%;
  border-style: solid;
  border-color: lightblue;
}

.NavBar {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.NavBar li {
  float: left;
}

.Link {
    display: block;
    color: white;
    text-align: center;
    padding: 5px 16px;
    text-decoration: none;
}

.Link:hover {
  border-bottom: medium dashed white;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



